.u-inline-select {
  display: inline-block;
  width: 130px;
}

#countMessage .cf-icon-svg {
  color: var(--green);
}

.warn {
  background-color: var(--gold-20);
  border-color: var(--gold);
}

.error {
  background-color: var(--red-20);
  border-color: var(--red);
}

#results-total .content-l__col {
  display: inline-flex;
  min-width: 12em;
}
#results-total .content-l__col > h1 {
  text-align: right;
}
#results-total .content-l__col > h4 {
  margin-top: 0.5em;
  margin-left: 1em;
}

#results-total .rural-cnt,
#results-total .not-rural-cnt,
#results-total .not-found-cnt,
#results-total .duplicate-cnt {
  font-size: 3rem;
}

.hide-print.right {
  float: right;
}

.o-table table {
  border: none;
  width: 100%;
}

.o-table-wrapper--scrolling table > tbody > tr:nth-child(even) > td {
  background: #fff;
}

tr.data + tr[class=""] {
  border-top: 2px solid var(--white);
}

.map {
  overflow: hidden;
  height: 200px;
  width: 100%;
}

.js-load-map svg {
  pointer-events: none;
}

/* stylelint-disable-next-line no-invalid-position-at-import-rule */
@media print {
  .hide-print {
    display: none;
  }
  .print-only {
    display: block;
  }
  #header,
  #footer,
  .button-more,
  .view-all {
    display: none;
  }
  h1 {
    font-size: 18px;
    margin-top: 0;
  }
  h2 {
    font-size: 14px;
  }
  h4 {
    font-size: 12px;
  }
  h5 {
    font-size: 12px;
  }
  p {
    font-size: 12px;
    line-height: 1;
  }
  #logo {
    width: 100px;
  }
  .full-print {
    width: 100%;
  }
  .report-date {
    color: var(--gray-60);
  }
  #results-total {
    background: var(--gray-10);
    padding: 3px;
  }
  #results-total a.no-decoration {
    border-bottom-style: none;
    border-bottom-width: 0;
    color: var(--black);
  }
  [class^=col-] {
    width: 100%;
  }
  table {
    font-size: 12px;
  }
  th,
  td {
    padding: 0.1em;
  }
  tbody tr.data.hide {
    position: relative;
    overflow: visible;
    clip: auto;
    height: auto;
    width: auto;
    margin: 0;
    padding: 0;
    border: 0;
  }
}
.u-hidden,
.print-only {
  display: none;
}